      desc: 'Недорогой отдых на Средиземноморье обеспечивает данное направление. Оно считается относительно новым, но уже завоевало своего клиента. Так, в летний период морское побережье порадует постоянно теплой температурой и мягким климатом. Густые леса обеспечивают чистоту воздуха и одновременно оздоравливают, очищают легкие и голову.В зимний же период тут особо популярны горнолыжные курорты, которые по стоимости достаточно дешевые, но по уровню сервиса и качеству спусков не уступают европейским аналогам. Удобство путешествия в Черногорию для наших туристов заключается в минимальном наборе документов, необходимых для этого.',
      desc2: 'Побережье Черногории имеет тёплый средиземноморский климат, который характеризуется сухим и жарким летом, а также мягкой и дождливой зимой. Центральные и северные области страны имеют континентальный климат, характер которого определяется высотной зональностью.',
