      desc2: 'Климат Ейска такой же, как и на всей приморской территории Краснодарского края: жаркое лето, теплое море, короткая, практически бесснежная, зима без особых морозов. В середине апреля в городе уже вовсю цветет весна, а холодная дождливая осень наступает только в октябре, после солнечного «бабьего лета». Дожди в теплое время года бывают редко, в основном в виде коротких ливней. Некоторое неудобство доставляют сильные ветра, но к ним быстро привыкаешь. Местный климат очень благотворно влияет на людей, страдающими и сердечнососудистыми, бронхо-легочными, кожными заболеваниями и различными аллергиями. Недаром Ейск называют климатическим курортом. В начале 20 века тут были найдены источники минеральных вод и залежи лечебной иловой грязи, что и послужило причиной для увеличения потока отдыхающих и мигрантов. Как и во всей активно развивающихся портовых городах, в Ейске существуют проблемы, связанные с загрязнением окружающей среды припортовыми промышленными предприятиями и нередкими авариями на судах, производящих транспортировку нефти, и других, опасных с точки зрения экологов, продуктов. Массу протеста у местных жителей вызвало открытие на территории города нефтеналивного терминала. В ноябре 2011г. в Ейске произошла утечка мазута из танкера, не очищенного от остатков ранее перевозимого сжиженного газа, и в момент заправки эти остатки выветрились в атмосферу. Подобное происшествие, к сожалению, не единично.',
